/**********************************
        Start About Section
**********************************/
.about{
    background-image: url(../../images/about-bg.png);
    background-size: cover;
    position: relative;
    z-index: 1;
    background-position: center center;
    .about-img{
        border-right: 2px solid $secondryColor;
        position: relative;
        &::after{
            content: '';
            right: -12px;
            top: 60px;
            // transform: translateY(-50%);
            position: absolute;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 10px 0 10px 10px;
            border-color: transparent transparent transparent #55b0fa;
        }
        @media (max-width: 991px) {
            border-right: none;
            &::after{
                border: none;
            }
        }
    }
    .about-content{
        padding-top: 50px;
        padding-left: 30px;
        .main-title{
            font-weight: bold;
            font-size: 38px;
        }
        span{
            font-size: 30px;
        }
        p{
            margin-top: 25px;
            margin-bottom: 30px;
            color: $textColor;
            line-height: 1.8;
        }
        ul{
            margin: 0;
            padding-left: 15px;
            li{
                color: $textColor;
                margin-bottom: 15px;
            }
        }
    }
}

/**********************************
        Start Rating Section
**********************************/
.rating{
    .box-0{
        background-image: linear-gradient(-234deg, #ea9d64 0%, #fa8936 100%);
    }
    .box-1{
        background-image: linear-gradient(-234deg, #6ae472 0%, #4bc253 100%);
    }
    .box-2{
        background: linear-gradient(to right top, #bc4e9c, #f80759);
    }
    .box-3{
        background: linear-gradient(to right top, #2193b0, #6dd5ed); 
    }
    .token-rating-box{
        background-color: #e85f70;
        padding: 40px;
        text-align: center;
        border-radius: 20px;
        transition: 0.4s;
        &:hover{
            transform: translateY(-20px);
        }
        @media (max-width: 991px) {
            margin-top: 40px;
        }
        h6{
            margin-top: 24px;
        }
        .rating-num{
            border: 1px solid #ddd;
            padding-top: 6px;
            padding-bottom: 6px;
            margin-top: 20px;
            font-size: 19px;
        }
        .btn{
            width: 100%;
            font-size: 19px;
        }
    }
}

/**********************************
        Start Platform Section
**********************************/
.platform{
    padding-top: 150px;
    margin-top: 50px;
    .about-content{
        width: 90%;
    }
    .about-img{
        border-left: 2px solid #55b0fa;
        border-right: none;
        padding-left: 40px;
        &::after{
            border-width: 10px 10px 10px 0;
            border-color: transparent #55b0fa transparent transparent;
            left: -10px;
        }
        @media (max-width: 991px) {
            margin-top: 40px;
            border-left: none;
            &::after{
                border: none;
            }
        }
        img{
            max-width: 100%;
        }
    }
}

/**********************************
        Start Uses Cases Section
**********************************/
.cases{
    .cases-box{
        margin-bottom: 30px;
        img{
            margin-right: 30px;
        }
        p{
            color: $textColor;
            margin-top: 15px;
        }
    }
}

/**********************************
        Start Contact Api Section
**********************************/
.contact-api{
    position: relative;
    .api-bg{
        position: absolute;
        left: 0;
        z-index: -1;
    }
    .api-content{
        .api-box{
            h5{
                margin-top: 15px;
            }
            &:nth-child(odd) {
                position: relative;
                bottom: 40px;
            }
        }
    }
}

/**********************************
        Start Download Section
**********************************/
.download{
    background-image: url(../../images/download/download-bg.png);
    background-size: cover;
    position: relative;
    z-index: 1;
    background-position: center center;
    background-repeat: no-repeat;
    .download-bg{
        padding-top: 30px;
    }
    .download-content{
        padding-bottom: 20px;
        h2{
            margin-bottom: 40px;
            font-size: 40px;
            width: 80%;
        }
        p{
            color: $textColor;
            width: 80%;
            margin-bottom: 10px;
        }
        img{
            margin-right: 40px;
            margin-top: 30px;
        }
    }
    .download-img{
        position: relative;
        bottom: 0;
        animation: animateLeftRight 4s linear infinite;
    }
}

@keyframes animateLeftRight {
    0%, 100%{
        transform: translateX(0);
    }
    50%{
        transform: translateX(40px);
    }
}

/**********************************
        Start Timeline Section
**********************************/

.timeline-section{
    // page container
    .container {
        max-width: 767px;
        margin: 50px auto;
    }
    
    // typography
    p {
        font-weight: 300;
        line-height: 1.5;
        font-size: 14px;
        opacity: .8;
    }
    // timeline style
    .timeline {
        position: relative;
        padding-left: 4rem;
        margin: 0 0 0 30px;
        color: white;
        &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 22px;
            width: 2px;
            height: 100%;
            background: #7b7b7b;
        }

        .timeline-container {
            position: relative;
            margin-bottom: 2.5rem;;

            .timeline-icon {
                position: absolute;
                left: -83px;
                top: 22px;
                width: 40px;
                height: 40px;
                border-radius: 50%;
                text-align: center;
                font-size: 2rem;
                background: #4f537b;

                i {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    font-size: 22px;
                }

                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                }
            }

            .timeline-body {
                // background: #27293d;
                border-radius: 3px;
                padding: 20px 20px 15px;
                box-shadow: 1px 3px 9px rgba(0,0,0, .1);

                &:before {
                    content: '';
                    background: inherit;
                    width: 20px;
                    height: 20px;
                    display: block;
                    position: absolute;
                    left: -10px;
                    transform: rotate(45deg);
                    border-radius: 0 0 0 2px;
                }

                .timeline-title {
                    margin-bottom: 1.4rem;

                    .badge {
                        background: #4f537b;
                        padding: 4px 8px;
                        border-radius: 3px;
                        font-size: 12px;
                        font-weight: bold;
                    }
                }

                .timeline-subtitle {
                    font-weight: 300;
                    font-style: italic;
                    opacity: .4;
                    margin-top: 16px;
                    font-size: 11px;
                }
            }

            // Primary Timeline
            &.primary {
                .badge,
                .timeline-icon {
                    background: #1d8cf8 !important;
                }
            }

            // Info Timeline
            &.info {
                .badge,
                .timeline-icon {
                    background: #11cdef !important;
                }
            }

            // Success Timeline
            &.success {
                .badge,
                .timeline-icon {
                    background: #00bf9a !important;
                }
            }

            // Warning Timeline
            &.warning {
                .badge,
                .timeline-icon {
                    background: #ff8d72 !important;
                }
            }

            // Dnager Timeline
            &.danger {
                .badge,
                .timeline-icon {
                    background: #fd5d93 !important;
                }
            }

        }
    }
}

/**********************************
        Start Advisor Section
**********************************/
.advisor{
    .advisor-single{
        background-color: #102171;
        padding: 40px 0;
        margin-top: 30px;
        .advisor-img{
            margin-bottom: 20px;
            img{
                width: 150px;
                height: 150px;
                border-radius: 100%;
                object-fit: cover;
                opacity: 0.6;
                border: 5px solid $mainColor;
                transition: 0.6s;
                &:hover{
                    opacity: 1;
                }
            }
        }
        .advisor-content{
            h4{
                font-weight: bold;
            }
            span{
                color: $textColor;
            }
            ul{
                margin-top: 30px;
                li{
                    position: relative;
                    list-style: none;
                    width: 120px;
                    height: 60px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
                    cursor: pointer;
                    .fa-brands{
                        font-size: 32px;
                        -webkit-text-stroke: 2px var(--clr);
                        color: transparent;
                    }
                }
            }
        }
    }
}


.advisor ul li::before{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    border-bottom: 3px solid var(--clr);
    transition: 0.5s ease-in-out;
    font-family: fontAwesome;
    text-align: center;
    line-height: 60px;
    font-size: 32px;
    color: var(--clr);
    overflow: hidden;
}

.advisor ul li:hover::before{
    height: 100%;
}

.advisor ul li:nth-child(1)::before{
    content: '\f16d';
}

.advisor ul li:nth-child(2)::before{
    content: '\f232';
}

.advisor ul li:nth-child(3)::before{
    content: '\f167';
}

.advisor ul li:nth-child(4)::before{
    content: '\f099';
}

/**********************************
        Start FAQ
**********************************/
.faq{
    .accordion-item{
        background-color: transparent;
        margin-bottom: 20px;
        .accordion-button{
            background-color: transparent;
            color: #fff;
            font-size: 22px;
            font-weight: 500;
            box-shadow: none;
            &::after{
                background-image: url(../../images/faq/plus.png);
            }
        }
    }
    img{
        max-width: 100%;
        @media (max-width: 991px) {
            margin-top: 40px;
        }
    }
}

.accordion-button:not(.collapsed)::after{
    background-image: url(../../images/faq/minus.png) !important;
    transform: none;
}

/**********************************
        Start Blog
**********************************/
.blog{
    .single-post{
        @media (max-width: 991px) {
            margin-top: 40px;
        }
        .post-img{
            height: 300px;
            img{
                height: 100%;
                width: 100%;
            }
        }
        .post-content{
            margin-top: 20px;
            a{
                color: #fff;
                transition: 0.5s;
                .post-title{
                    margin: 15px 0;
                }
                &:hover{
                    color: $mainColor;
                }
            }
            .post-date{
                color: $textColor
            }
            .post-details{
                margin-bottom: 10px;
                color: $textColor;
            }
        }
    }
}

/**********************************
        Start Contact
**********************************/
.contact{
    background-image: url(../../images/contact-bg.jpg);
    background-size: cover;
    position: relative;
    z-index: 1;
    background-position: center center;
    background-repeat: no-repeat;
    margin-top: 150px;
    padding-top: 80px;
    padding-bottom: 80px;
    .contact-header{
        .icon{
            position: relative;
            i{
                display: inline-block;
                height: 49px;
                width: 49px;
                line-height: 49px;
                text-align: center;
                background: #0195F9;
                font-size: 22px;
                color: #fff;
                border-radius: 100%;
            }
            &::after{
                position: absolute;
                content: "";
                top: -8px;
                left: -8px;
                height: 65px;
                width: 65px;
                z-index: -1;
                background: rgb(55,163,254,0.5);
                border-radius: 100%;
            }
        }
        span{
            font-size: 20px;
            font-weight: 500;
            margin-left: 28px;
            line-height: 40px;
        }
    }
    .contact-form{
        margin-top: 60px;
        .form-control{
            box-shadow: none;
            background-color: #1E1D56;
            margin-top: 30px;
            border: none;
            color: #FFF;
        }
        input{
            height: 50px;
        }
        textarea{
            height: 220px;
        }
    }
}