
/* CSS */
.btn {
    font-size: 16px;
    position: relative;
    color: $mainColor;
    cursor: pointer;
    z-index: 1;
    text-align: center;
    display: inline-block;
    padding: 14px 40px;
    margin-right: 40px;
    margin-top: 20px;
    font-weight: 500;
}

.btn::after,
.btn::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -99999;
    transition: all .4s;
}

.btn:hover{
    transition: all .4s;
    color: #fff !important;
}

.btn::before {
    transform: translate(0%, 0%);
    width: 100%;
    height: 100%;
    background: #fff;
    border-radius: 10px;
}

.btn::after {
    transform: translate(10px, 10px);
    width: 35px;
    height: 35px;
    background: $mainColor;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border-radius: 50px;
}

.btn:hover::before {
    transform: translate(5%, 20%);
    width: 110%;
    height: 110%;
}

.btn:hover::after {
    border-radius: 10px;
    transform: translate(0, 0);
    width: 100%;
    height: 100%;
    color: #fff;
}

.btn:active::after {
    transition: 0s;
    transform: translate(0, 5%);
}