
/**********************************
        Start Footer 
**********************************/
footer {
    padding-top: 50px;
    padding-bottom: 50px;
    background-color:#0b0b47;
    font-family: "Josefin Sans", sans-serif;
    .name{
        color: #fff;
        font-weight: 600;
        font-size: 28px;
        padding-left: 5px;
        letter-spacing: 2px;
    }
    .social-link{
        margin-top:6px;
        a{
            display:inline-block;
            font-size:15px;
            color:#fff !important;
            height:40px;
            width:40px;
            background:rgba(255,255,255,0.1);
            border-radius: 50%;
            text-align: center;
            margin-right:10px;
            i{
                line-height:40px !important
            }
        }
    }
    .footer-down{
        padding-top: 40px;
        border-top: 1px solid rgba(255,255,255,0.1);
        .footer-logo{
            h2{
                color:#fff;
            }
            p{
                margin-top: 26px;
                color:#707b8e;
            }
        }
        h5{
            margin-bottom: 28px;
            color:#fff;
        }
        ul{
            li{
                margin-bottom: 10px;
                a{
                    color:#707b8e !important;
                }
            }
        }
        p{
            color:#707b8e;
        }
        form{
            input{
                display: inline-block !important;
                width: 85% !important;
                background: none !important;
            }
            button{
                float :right;
                width: 50px;
                height: 38px;
                background:#00BFE7;
                color:#fff;
                border: none;
            }
        }
    }
    .footer-section-tow{
        margin-top: 30px;
        padding-top: 30px;
        border-top: 1px solid #1f2024;
        i{
            color:red;
        }
        p{
            color:#c4c4c4;
        }
    }
}

footer .footer-down form .form-control:focus{border-color:none}