::selection {
    background: $mainColor;
    color: #fff;
}

body{
    background-color: $backgroundColor;
    color: #fff;
    overflow-x: hidden;
    a{
        text-decoration: none;
    }
    .section-top{
        padding-top: 120px;
    }
    .mt-160{
        padding-top: 160px;
    }
    .title-section{
        color: #fff;
        padding-bottom: 80px;
        span{
            background-color: #081B5E;
            padding: 12px 30px;
            color: $secondryColor;
            font-size: 17px;
            font-weight: 500;
        }
        .title{
            margin-top: 26px;
            margin-bottom: 14px;
            font-weight: bold;
        }
        p{
            width: 40%;
            margin: 0 auto;
            @media (max-width: 991px) {
                width: 80%;
            }
        }
    }
}