header{
    .header-content{
        color: #fff;
        h1{
            margin-bottom: 20px;
            font-size: 58px;
            @media (max-width: 991px) {
                font-size: 46px;
            }
            @media (max-width: 767px) {
                font-size: 36px;
            }
            span{
                color: $mainColor;
            }
        }
        p{
            line-height: 1.8;
            margin-bottom: 80px;
            color: $textColor;
        }
    }
    .header-img{
        width: 100%;
        @media (max-width: 991px) {
            margin-top: 80px;
        }
        .main-header-img{
            animation: animateUpDown 6s linear infinite;
            width: 100%;
        }
    }
    .float-header-img{
        position: absolute;
        right: 120px;
        width: 180px;
        animation: animateRotate 8s linear infinite;
    }
}

@keyframes animateUpDown {
    0%, 100%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(40px);
    }
}

@keyframes animateRotate {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}