.navbar{
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 9;
    min-height: 70px;
    .navbar-brand{
        color: #fff;
        font-weight: 600;
        font-size: 28px;
        padding-left: 20px;
        img{
            width: 70px;
        }
    }
    .navbar-nav{
        .nav-link{
            color: #fff;
            font-size: 15px;
            text-transform: capitalize;
            font-weight: bold;
            @include prefixer(transition, all .3s ease, webkit moz o);
            &:hover{
                color: $mainColor;
            }
        }
    }
    .navbar-toggler{
        .icon-bar{
            background-color: $mainColor;
            padding: 8px 14px;
            i{
                color:#fff
            }
        }
    }
}

.navbar-toggler:focus{
    box-shadow: none;
}

.navbar-bg{
    background: #060652;
    @include prefixer(box-shadow, 0px 10px 40px rgba(0, 0, 0, 0.05), webkit moz o);
    padding: 0;
    border-bottom: 1px solid #333853;
    position: fixed;
    top: -100px;
    left: 0;
    width: 100%;
    @include prefixer(transform, translateY(100px), webkit moz o);
    @include prefixer(transition, transform .5s ease, webkit moz o);
}
.active{
    color: $mainColor !important;
}

.navbar-img-bg .navbar-nav .nav-link{
    color: #fff;
}

.navbar-expand-lg .navbar-collapse{
    justify-content: space-evenly;
}